@font-face {
    font-family: 'S-CoreDream-3Light';
    src: url('https://cdn.jsdelivr.net/gh/projectnoonnu/noonfonts_six@1.2/S-CoreDream-3Light.woff') format('woff');
    font-weight: normal;
    font-style: normal;
}

* { 
    margin: 0; 
    padding: 0; 
    box-sizing: border-box; 
 } 
li { 
    list-style: none; 
} 
a { 
    text-decoration: none; 
    color: inherit; 
} 
body { 
    color: #333; 
    line-height: 1; 
    font-family: 'S-CoreDream-3Light';
} 

.news_list { 
    max-width: 767px; 
    width: calc(100% - 20px); 
    margin: 0 auto; 
    margin-top: 30px;
} 
.news_list .news_item { 
    display: flex; 
    align-items: center;
    margin-bottom: 20px;
    padding-bottom: 20px;
    border-bottom: 1px solid #ccc;

} 

.news_list .news_item figure { 
 /*    width: 200px; 
    height: 200px;  */
    margin-right: 20px;
} 
.news_list .news_item figure img { 
    width: 200px; 
    height: 200px; 
    object-fit: cover;
    border-radius: 10px;
}
.news_list .news_item .news_text {
    /* width: calc(100% - 200px);  */
}
.news_list .news_item .news_text h4 {
    font-size: 23px;
    line-height: 1.3;
    display: -webkit-box;
    -webkit-line-clamp: 2;
    -webkit-box-orient: vertical;
    overflow: hidden;
}
.news_list .news_item .news_text p {
    font-size: 20px;
    margin-top: 30px;
    line-height: 1.3;
    display: -webkit-box;
    -webkit-line-clamp: 3;
    -webkit-box-orient: vertical;
    overflow: hidden;
}
.news_list .news_item .news_text span {
    margin-top: 10px;
    display: block;
    color: #888;
}

@media screen and (max-width: 460px) {
    .news_list .news_item {
        display: block;
    }
    .news_list .news_item figure {
        margin: 0;
        margin-bottom: 20px;
    }
    .news_list .news_item figure img {
        width: 100%;
    }
    .news_list .news_item .news_text h4 {
        font-size: 20px;
    }
    .news_list .news_item .news_text p {
        font-size: 15px;
        margin-top: 15px;
    }
}